/* global React ReactDOM ChannelList fetchChannelData */


/*
 * fetch and re-render channel components with latest data
 */
(function () {

	// extract channel data from JSON string in custom data attribute
	const element = document.getElementById("channel_data");
	if (element) {

		const channel = JSON.parse(element.innerText);

		fetchChannelData(channel).then(data => {

			ReactDOM.render(
				React.createElement(
					ChannelList,
					{ "channel": channel, "data": data }
				),
				document.getElementById("channel_list_container")
			);

		}).catch(err => {
			throw err;
		});

	}

}());
